:root{
    /*COLORS*/    
    --gbg: rgb(197, 197, 197); 
    --dgbg: #1d1b19;
    --dc: #987750;
    --gc: #888;/*     --gc: rgba(255, 255, 255, .2);  */
    --wc: #ddd;  
    --hc: #b3aca7;
    --grc: rgb(54, 182, 37);  
    --rc : rgb(151 39 39);
    --white:rgb(255, 255, 255);
    --bspinner:#221b15;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;
    color: var(--gc);
    font-weight: 400;
}

body {
    background-color: var(--dgbg);
}

li {
    list-style: none;
}

.key {
    font-size: 18px;
    letter-spacing: 1px;
    color: var(--gc);
    font-weight: 500;
}

.value {
    font-size: 18px;
    letter-spacing: 0;
    color: var(--wc);
    font-weight: 400;
}


h1, h2, h3, h4, h5, h6 {
    line-height: 1.2;
    font-weight: 600;
    color: var(--wc);
}

h2 {
    font-size: 45px;
    font-weight: 400;
}

h3 {
    font-size: 30px;
}

h5 {
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
}


p {
    font-size: 20px;
    line-height: 44px;
    letter-spacing: 0;
    color: var(--gc);
}

.GOLD {
    font-weight: 500;
    letter-spacing: 1px;
    color: var(--dc);
}

.sign {
    font-family: 'Smooch', cursive;
    font-size: 3rem;
    margin: 2rem;
}

.flex {
    display: flex;
}

.flex-align-center {
    display: flex;
    align-items: center;
}

.flex-centerAll {
    display: flex;
    align-items: center;
    justify-content: center;
}

.text-decore-none {
    text-decoration: none;
}


.btn {
    width: 150px;
    height: 30px;
    padding: 0;
    margin: -5px 0px 0px 0px;
    background: transparent;
    font-size: 0.875em;
    border: 2px solid var(--gc);
    color: var(--wc);
    outline: none;
    cursor: pointer;
}

.btn:hover {
    border: solid 2px var(--dc);
    color: var(--dc);
}

.input {
    border: solid 1px var(--gc);
    padding: 1rem;
    background-color: var(--dgbg);
    font-size: 20px;
}
.width-100{
    width: 100%;
}
.input:hover {
    border: solid 1px var(--wc);
}


.btn-form {
    margin: 0.5rem auto;
    width: 100%;
    height: 45px;
}

.spinner{
    position: relative;
    height:400px;
    width:400px;
    overflow:hidden;
    border-radius: 100%;
}

.spinner-card{
    position: relative;
    height:500px;
    width:380px;
    overflow:hidden;
    border-radius: 100%;
}
.spinner-card{
    position: relative;
    height:500px;
    width:380px;
    overflow:hidden;
    border-radius: 100%;
}
.spinner__fondo{
    background: var(--bspinner);
    width:100%;
    height:100%;
    position:absolute;
    top:0;
    opacity:50%;
    z-index: 21;
}
.spinner__imagen{
    width:100%;
    height:100%;
    z-index: 20;
}

