}
.MyPokemonScreenHeader__blink{
    color: var(--grc);
    -webkit-animation: blink 2.5s linear infinite;
            animation: blink 2.5s linear infinite;
}

@-webkit-keyframes blink{
    0%{opacity: 0;}
    50%{opacity: .5;}
    100%{opacity: 1;}
    }

@keyframes blink{
    0%{opacity: 0;}
    50%{opacity: .5;}
    100%{opacity: 1;}
    }
